/* Alinhar o conte√∫do dentro da container */
/* Para o DatePicker (input de data) */
.custom-datepicker-input {
    width: 80%; /* Ajuste a largura conforme necess√°rio */
    max-width: 400px; /* Limita a largura m√°xima */
    padding: 10px;
    margin-top: 30px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
    outline: none;
    transition: border-color 0.3s;
    margin-bottom: 20px; /* Espa√ßamento inferior */
}

/* Foco do DatePicker */
.custom-datepicker-input:focus {
    border-color: var(--cor-primaria);
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

/* Estilo do calend√°rio */
.custom-calendar {
    background-color: #f8f9fa;
    border-radius: 8px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);

}

.custom-calendar .react-datepicker__day--selected {
    background-color: var(--cor-primaria);
    color: #fff;
    border-radius: 50%;
}

.custom-calendar .react-datepicker__day--today {
    font-weight: bold;
    color: var(--cor-secundaria);
}

/* Container para os bot√µes de hor√°rio (alinhamento e responsividade) */
/* Ajuste na lista de hor√°rios */
/* Ajuste na lista de hor√°rios */
.hour-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; /* Centraliza os bot√µes horizontalmente */
    gap: 10px;
    width: 80%;
    padding: 10px;
    /* Remover limite de altura para n√£o cortar conte√∫do */
    max-height: none; 
    overflow-y: auto;
}

/* Ajuste nos bot√µes para um layout mais controlado */
/* Bot√µes com largura controlada */
.hour-button {
    background-color: var(--cor-secundaria);
    color: white;
    border: none;
    padding: 10px 20px;
    margin: 5px;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    transition: all 0.3s ease;
    max-width: 150px; /* Limita a largura m√°xima dos bot√µes */
    min-width: 100px; /* Garante que o bot√£o tenha pelo menos 100px */
    text-align: center; /* Centraliza o texto */
}




/* Estilo dos bot√µes de hor√°rio */
