.hour-button:hover {
    background-color: var(--cor-primaria); /* Cor mais escura ao passar o mouse */
    transform: scale(1.05);
}

.hour-button:active {
    background-color: var(--cor-primaria); /* Cor ao clicar */
    transform: scale(0.95);
}

