/* Estilo da container de hor√°rios */
/* Combine e ajuste as propriedades da .hour-container */
.hour-container {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center; /* Garante que os bot√µes fiquem centralizados */
    width: 100%;
    padding: 10px;
    max-height: 400px; /* Limita a altura do cont√™iner */
    overflow-y: auto; /* Adiciona o scroll vertical */
}


/* Estilo da lista de hor√°rios */
.hour-list {
    justify-content: center;
    align-items: center;
    max-height: 400px; /* Ajuste conforme necess√°rio */
    overflow-y: auto;
}


/* Estilo dos bot√µes de hor√°rio */
.hour-button {
    padding: 10px;
    background-color: #f0f0f0;
    border: 1px solid #ddd;
    border-radius: 5px;
    cursor: pointer;
    text-align: center;
}

.hour-button:hover {
    background-color: #ddd;
}

/* Ajuste para telas pequenas */
@media (max-width: 600px) {
    .hour-container {
        justify-content: flex-start; /* Ajusta para que os bot√µes fiquem alinhados √† esquerda em telas pequenas */
    }

    .hour-button {
        min-width: 80px; /* Bot√µes podem ser menores em telas pequenas */
    }
}
